@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200&display=swap);

.App_app__3OoO3 {
    font-family: 'Montserrat', 'Courier New', sans-serif;
    text-align: center;
}

.App_app__3OoO3 button {
    background-color: darkorange;
    border-top: 2px solid rgba(244, 179, 80, 1);
    border-right: 2px solid rgba(232, 126, 4, 1);
    border-bottom: 2px solid rgba(232, 126, 4, 1);
    border-left: 2px solid rgba(244, 179, 80, 1);
    color: rgba(255, 255, 255, 0.8);
    font-weight: bold;
    height: 40px;
}

.App_app__3OoO3 button:hover {
    background-color: rgba(232, 126, 4, 1);
    color: #fff;
    cursor: pointer;
}

.App_app__3OoO3 button:active {
    border-top: 2px solid rgba(232, 126, 4, 1);
    border-left: 2px solid rgba(232, 126, 4, 1);
    border-right: 2px solid rgba(244, 179, 80, 1);
    border-bottom: 2px solid rgba(244, 179, 80, 1);
    color: #fff;
    cursor: pointer;
}

.App_header__2g8RA {
    border-bottom: 1px solid lightgrey;
    color: rebeccapurple;
    font-weight: bolder;
    margin-bottom: 30px;
}

.App_recipesList__354cF {
    display: grid;
    grid-gap: 40px;
    grid-template-columns: 1fr 1fr 1fr;
    justify-content: space-between;
    margin: 30px auto;
    width: 90%;
}

/*recipeModal window*/
.App_modal__xbnQ9 {
    align-items: center;
    background-color: rgba(228, 233, 237, 0.7);
    bottom: 0;
    display: flex;
    justify-content: center;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 1;
}

.App_modalCont__GWO_5 {
    background-color: #fff;
    display: flex;
    flex-direction: row;
    max-height: 100vh;
    min-height: 500px;
    outline: 2px solid lightgrey;
    overflow-y: auto;
    position: relative;
    width: 700px;
}

.App_modalDescriptionCont__1y7Qi {
    display: flex;
    flex-direction: column;
}

.App_modalDescriptionCont__1y7Qi > :last-child {
    margin-top: auto;
}

.App_modalTitle__l-VQj {
    color: darkorange;
    font-size: large;
    font-weight: bold;
    padding: 15px;
}

.App_modalDescription__3e6_5 {
    font-size: large;
    padding: 0 25px 10px;
}

.App_modalCont__GWO_5 img {
    height: 100%;
    object-fit: cover;
    width: 350px;
}

/*close icon*/
.App_close__2vdq1 {
    height: 20px;
    opacity: 0.3;
    position: absolute;
    right: 20px;
    top: 10px;
    width: 20px;

}

.App_close__2vdq1:hover {
    cursor: pointer;
    opacity: 1;
}

.App_close__2vdq1:before, .App_close__2vdq1:after {
    background-color: #333;
    content: ' ';
    height: 33px;
    left: 15px;
    position: absolute;
    width: 2px;
}

.App_close__2vdq1:before {
    transform: rotate(45deg);
}

.App_close__2vdq1:after {
    transform: rotate(-45deg);
}

/*cart*/
.App_cart__3oQ1u {
    align-items: center;
    background-color: rgba(255, 255, 255, 1);
    border: 1px solid grey;
    bottom: 5px;
    display: flex;
    height: 70px;
    justify-content: center;
    position: fixed;
    right: 5px;
    width: 250px;
}

.App_cartCount__3ZJUl {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.App_cartCount__3ZJUl > :first-child {
    font-weight: bolder;
    line-height: 50px;
}

.App_cartCount__3ZJUl img {
    height: 50px;
}

/*responsive*/
@media screen and (max-width: 800px) {
    .App_recipesList__354cF {
        grid-template-columns: 1fr 1fr;
        width: 100%;
    }

    .App_modal__xbnQ9 {
        background-color: #fff;
        height: 100vh;
    }

    .App_modalCont__GWO_5 {
        flex-direction: column;
        height: 100vh;
        outline: none;
        width: 100%;
    }

    .App_modalCont__GWO_5 img {
        width: 100%
    }

    .App_modal__xbnQ9 button {
        bottom: 0;
        position: fixed;
        width: 100%
    }

    .App_modalDescription__3e6_5 {
        padding-bottom: 40px
    }

    .App_recipesList__354cF {
        grid-gap: 20px
    }
}

@media screen and (max-width: 450px) {
    .App_recipesList__354cF {
        grid-template-columns: 1fr
    }

    .App_cart__3oQ1u {
        bottom: 0;
        right: -1px;
        width: 100%;
    }
}



.Recipe_recipe__1Vl0h {
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    min-height: 550px;
    outline: 2px solid lightgrey;
}
.Recipe_recipe__1Vl0h:hover {
    box-shadow: 0 0 5px rgba(0,0,0,0.5);
    cursor: pointer;
}
.Recipe_recipe__1Vl0h img {
    width: 100%;
}
.Recipe_recipe__1Vl0h> :last-child {
    margin-top: auto;
}
.Recipe_title__jMJZi {
    color: darkorange;
    font-weight: bold;
    padding: 10px;
}
.Recipe_description__3hDbp {
    padding: 0 15px 15px;
}



